import axios from 'axios'
axios.defaults.baseURL = 'http://open.duyiedu.com'
axios.interceptors.request.use(function (config) {
  // 
  console.log(config)
  config.params = {
    ...config.params,
    appkey: '18487184250_1559135253756'
  }
  return config
}, function (error) {
// 
  return Promise.reject(error)
})

export default axios



